import {model} from './model'
import {title, columns, text} from "./templates";
import './styles/main.css'

const $site = document.querySelector('#site')

model.forEach(block => {
    let html = ''

    switch (block.type) {
        case 'title':
            html = `
                   ${title(block)}
            `
            break
        case 'text':
            html = `
       ${text(block)}
`
            break
        case 'columns':
            html = `
            ${columns(block)}
            `
            break
    }

    $site.insertAdjacentHTML('beforeend', html)

})