export function title(block) {
    return `
        <div class="row">
            <div class="col-sm">
                <h1>${block.value}</h1>
            </div>
        </div>
        `
}

export function text(block) {
    return `
         <div class="row">
            <div class="col-sm">
                <p>${block.value}</p>
            </div>
        </div>
        `
}

export function columns(block) {
    const res = block.value.map(el => `<div class="col-sm">${el}</div>`)
    return `
        <div class="row">
        ${res.join('')}
        </div>
        `
}